body {
    padding: 0;
    font-family: sans-serif;
}

.big-loader {
    -webkit-animation: fadein 2s;
            animation: fadein 2s;
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@-webkit-keyframes fadein { /* Safari and Chrome */
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}



